@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Montserrat', 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #1E1F25;
}
ul.nav{
	display: flex;
  flex-wrap: wrap;
  box-sizing: content-box;
	width: 100%;
	height: 50px;
  padding: 25px 0;
	margin: auto;
	max-width: 1000px;
	justify-content: space-between;
	text-align: center;
}
ul.nav li {
	padding: 1rem 2rem 1.15rem;
  text-transform: uppercase;
  cursor: pointer;
  color: #ebebeb;
	min-width: 80px;
	margin: auto;
}

ul.nav li a {
  color: #ebebeb;
}

ul.nav li:hover {
  background-image: url('https://scottyzen.sirv.com/Images/v/button.png');
  background-size: 100% 100%;
  color: #27262c;
  -webkit-animation: spring 300ms ease-out;
          animation: spring 300ms ease-out;
  text-shadow: 0 -1px 0 #ef816c;
	font-weight: bold;
}
ul.nav li:active {
  -webkit-transform: translateY(4px);
          transform: translateY(4px);
}

@-webkit-keyframes spring {
  15% {
    -webkit-transform-origin: center center;
    -webkit-transform: scale(1.2, 1.1);
  }
  40% {
    -webkit-transform-origin: center center;
    -webkit-transform: scale(0.95, 0.95);
  }
  75% {
    -webkit-transform-origin: center center;
    -webkit-transform: scale(1.05, 1);
  }
  100% {
    -webkit-transform-origin: center center;
    -webkit-transform: scale(1, 1);
  }
}

@keyframes spring {
  15% {
    -webkit-transform-origin: center center;
    -webkit-transform: scale(1.2, 1.1);
  }
  40% {
    -webkit-transform-origin: center center;
    -webkit-transform: scale(0.95, 0.95);
  }
  75% {
    -webkit-transform-origin: center center;
    -webkit-transform: scale(1.05, 1);
  }
  100% {
    -webkit-transform-origin: center center;
    -webkit-transform: scale(1, 1);
  }
}
.page-footer {
  background-color: #1E1F25;
}
.movies {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(220px, 1fr));
  grid-gap: 1rem;
  gap: 1rem;
}
.movie {
  background-color: #1E1F25;
  box-shadow: none;
}
.movie:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  background-color: #1E1F25;
  box-shadow: 0 0 10px rgba(0,0,0,0.9);
}
.movie img {
  height: 350px;
}

.card .card-content {
  display: flex;
  flex-direction: column;
  flex-flow: column-reverse;
  padding: 10px 10px 10px 15px;
}
.search-btn {
  position: absolute;
  top: 4px;
  right: 0.5rem;
}
.filter {
  display: flex;
  grid-gap: 2rem;
  gap: 2rem;
}
.row .filter label span:after {
  border: #607d8b;
  background-color: #607d8b;
}
.input-field input.validate {
  font-family: -apple-system, BlinkMacSystemFont, 'Montserrat', 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  background-color: white;
  box-sizing: border-box;
  border-radius: 5px;
  padding: 0 0 0 15px;
}
.input-field input.validate::-webkit-input-placeholder {
  font-family: -apple-system, BlinkMacSystemFont, 'Montserrat', 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
}
.input-field input.validate:-ms-input-placeholder {
  font-family: -apple-system, BlinkMacSystemFont, 'Montserrat', 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
}
.input-field input.validate::placeholder {
  font-family: -apple-system, BlinkMacSystemFont, 'Montserrat', 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
}
.input-field button {
  font-family: -apple-system, BlinkMacSystemFont, 'Montserrat', 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
}
.content {
  /* padding: 1.5rem 0; */
  min-height: calc(100vh - 70px - 64px);
}
